// src/components/Paginator/Paginator.tsx
import React from 'react';
import { Stack, Pagination, PaginationProps as MuiPaginationProps } from '@mui/material';

export interface PaginatorProps {
  /**
   * Nmero total de pginas
   */
  count: number;
  /**
   * Pgina actual (1-based)
   * Se no fornecido, o componente ser uncontrolled
   */
  page?: number;
  /**
   * Callback quando o utilizador muda de pgina
   */
  onChange?: (event: React.ChangeEvent<unknown>, page: number) => void;
  /**
   * Nmero de pginas a mostrar ao lado da pgina actual
   * @default 1
   */
  siblingCount?: number;
  /**
   * Nmero de pginas de fronteira a mostrar (incio/fim)
   * @default 1
   */
  boundaryCount?: number;
  /**
   * Mostrar botes Primeiro e ltimo
   * @default false
   */
  showFirstButton?: boolean;
  showLastButton?: boolean;
  /**
   * Props extras para o <Pagination> do MUI
   */
  muiProps?: Omit<MuiPaginationProps, 'count' | 'page' | 'onChange' | 'siblingCount' | 'boundaryCount' | 'showFirstButton' | 'showLastButton'>;
}

export const Paginator: React.FC<PaginatorProps> = ({
  count,
  page,
  onChange,
  siblingCount    = 1,
  boundaryCount   = 1,
  showFirstButton = false,
  showLastButton  = false,
  muiProps        = {},
}) => (
  <Stack alignItems="center" sx={{ my: 2 }}>
    <Pagination
      count={count}
      page={page}
      onChange={onChange}
      siblingCount={siblingCount}
      boundaryCount={boundaryCount}
      showFirstButton={showFirstButton}
      showLastButton={showLastButton}
      color="primary"
      {...muiProps}
    />
  </Stack>
);

export default Paginator;
